/******************************************************************************
 *
 * Default styles
 *
 ******************************************************************************/

* {
  font-family: Helvetica, Arial;
  margin: 0;
  padding: 0;
}

html {
  font-size: 15px;
  min-height: 100%;
  overflow-y: scroll;
  overflow: -moz-scrollbars-vertical;
}

body {
  //  background: linear-gradient(140deg, #c7bfa4d4, #d3973ca1);
  background-color: #d3c8ac;
  height: 100%;
}

h1,
h1 a,
h1 a:hover,
h2,
h3,
h4 {
  color: #000;
  font-family: 'Lato', Helvetica, Arial;
  text-decoration: none;
}

a {
  color: #582c08;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
  color: #a05013;
}

ul {
  list-style-type: square
}

ol li,
ul li {
  margin-left: 1.3em;
}

blockquote {
  background: #d1bb94;
  border-left: 4px solid #7b5836;
  display: flex;
  margin-top: 10px;
  padding-bottom: 20px;
  padding-left: 20px;
}

blockquote p {
  color: #444;
  position: relative;
  top: 8px;
}

small {
  display: block;
  margin: 0 auto;
  max-width: 750px;
  text-align: center;
}

hr {
  background-color: #ccc;
  border: none;
  height: 1px;
  margin-bottom: 25px;
  margin-top: 25px;
}

.hidden {
  display: none;
}

/******************************************************************************
 *
 * Table of contents (experimental)
 *
 ******************************************************************************/

.toc {
  list-style: none;
  margin: 0;
  padding: 0;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  > li {
    margin: 0;
    padding: 0 0 0 1em;
    position: relative;
  }

  li .caret {
    cursor: pointer;
    user-select: none;
    display: inline-block;
    padding-left: 1.1em;
  }

  li .caret::before {
    content: "▶";
    position: absolute;
    left: 0;
    top: 0;
    font-size: 0.9em;
    transition: transform 0.2s;
  }

  li.expanded .caret::before {
    content: "▼";
  }

  ul {
    display: none;
  }

  li.expanded ul {
    display: block;
  }
}

/******************************************************************************
 *
 * Display width
 *
 ******************************************************************************/

.page,
.footer,
.header > .inner {
  margin: 0 auto;
  width: 900px;
}

/******************************************************************************
 *
 * Header
 *
 ******************************************************************************/

.header {
  background: #8b4100ab;

  > .inner {
    align-items: center;
    display: flex;
    padding: 19px 0 21px 0;

    > .links {
      display: flex;
      flex-grow: 1;
      justify-content: end;
    }
  }

  hr {
    border: 0;
    border-bottom: 1px solid #bbb;
    margin: 0;
    padding: 0;
  }

  h1,
  h1 a {
    color: #fff;
    flex-grow: 1;
    font-size: 22px;
    font-style: italic;
    font-weight: 400;
  }

  .links {
    margin-right: 15px;

    > a {
      color: #fff;
      font-family: 'Lato', Helvetica, Arial;
      font-size: 15px;
      font-weight: 600;
      margin-right: 11px;
      padding: 4px;
      text-transform: uppercase;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  p {
    color: #000;
    font-family: 'Lato', Helvetica, Arial;
    font-size: 16px;
    margin: 10px 0 14px 0;
  }
}

/******************************************************************************
 *
 * Content
 *
 ******************************************************************************/

.content {
  display: inline-block;
  padding-top: 6px;
  width: 100%;

  h1,
  h1 a,
  h2,
  h2 a,
  h3,
  h3 a {
    font-family: 'Lato', Helvetica, Arial;
    color: #582c07;
  }

  h1 {
    font-style: italic;
    font-size: 23px;
    margin: 18px 5px 0 0;
    padding-bottom: 0.15em;
  }

  img {
    display: block;
    height: auto;
    max-width: 100%;
    margin: 23px auto;
  }

  h1 a:hover,
  h2 a:hover,
  h3 a:hover {
    color: #a05013;
  }

  h1 span {
    display: none;
  }

  .time {
    color: #8c694b;
    display: block;
    font-style: italic;
    font-size: 12px;
    font-weight: normal;
    line-height: 1.5em;
    margin: 8px 0 14px 0;
    position: relative;
    left: -1px;
  }

  h2 {
    font-weight: 700;
    font-size: 20px;
    margin: 1.3em 0 0 0;
  }

  h3 {
    font-size: 18px;
    font-weight: 700;
    margin: 1.2em 0.8em 0.5em 0px;
  }

  p,
  ol,
  ul,
  ol li,
  ul li {
    font-size: 16px;
  }

  p {
    line-height: 26px;
    margin: 11px 1px 4px 0px;
  }

  ol,
  ul {
    clear: both;
    margin: 0.4em 0.6em 0.4em 1.4em;
  }

  ol.links,
  ul.links {
    margin-top: 15px;
  }

  ol li,
  ul li {
    line-height: 23px;
    margin-bottom: 3px;

    small {
      margin-bottom: 10px;
    }

    p > small {
      margin-top: 7px;
      margin-bottom: 12px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  ol.links li,
  ul.links li {
    margin-bottom: 0.5em;
  }

  code,
  code span {
    font-family: "SFMono-Regular", "Menlo", " Monaco", "Consolas", "Liberation Mono", "Courier New", monospace;
    font-size: 14px;
    line-height: 1.4em;
  }

  p > code,
  li > code {
    font-size: 14px;
    line-height: 14px;
    overflow: hidden;
    display: inline;
    background: #fff;
    border: 1px solid #ddd;
    padding-top: 2px;
    padding-left: 4px;
    padding-right: 3px;
    margin-right: 1px;
  }

  pre.prettyprint {
    background: #f5f5f5;
    padding: 1em 0.9em;
    border-radius: 4px;
    border-color: #aaa;
    margin: 1em 0;
    overflow: auto;
  }

  .posts {
    max-width: 670px;
  }

  .post {
    display: inline-block;
    clear: both;
    margin-top: 10px;
    margin-bottom: 5px;
    border-bottom: 1px solid #b57e46;
    padding-bottom: 24px;
    width: 100%;
  }

  .post:last-child {
    border-bottom: none;
    padding-bottom: 0;
  }

  .preview {
    border: 1px solid #aaa;
    float: left;
    margin: 12px 13px 11px 0;
    vertical-align: bottom;
  }

  .readmore {
    display: inline-block;
    margin-top: 11px;
    margin-bottom: 11px;
  }

  .MathJax_Display {
    padding-left: 1.5em;
    text-align: left !important;
  }

  figure {
    border: 1px solid #ccc;
    background: #fff;
    display: table;
    float: right;
    position: relative;
    left: 30px;
    padding: 10px;
    margin-top: 4px;
    margin-bottom: 28px;
    margin-left: 2px;
    width: 1px;

    img {
      padding: 5px 9px 10px 9px;
      display: table-row;
      vertical-align: bottom;
    }

    figcaption {
      font-size: 12px;
      padding: 0em 1em 0.9em 1em;
      display: table-row;
      vertical-align: bottom;
    }
  }
}

/******************************************************************************
 *
 * Sidebar
 *
 ******************************************************************************/

.sidebar {
  float: right;
  padding: 1em 0 0 0;
  width: 200px;

  h1 {
    clear: both;
    // color: #353535;
    font-family: 'Lato', Helvetica, Arial;
    font-style: italic;
    font-size: 16px;
    margin: 15px 0 8px 0;
  }

  p,
  ol,
  ul,
  ol li,
  ul li {
    font-size: 13px;
  }

  h3 {
    font-size: 12px;
    font-weight: bold;
    color: #666;
  }

  p {
    line-height: 1.6em;
    margin-top: 0.8em;
    margin-bottom: 0.2em;
    max-width: 190px;
  }

  ol,
  ul {
    margin: 0 0 0.8em 0.4em;

    li {
      line-height: 1.4em;
      margin-bottom: 0.3em;
      margin-right: 0;
    }
  }

  .avatar {
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 6px;
    background: #fff;
  }

  img.icon {
    float: left;
    margin-top: 4px;
    margin-right: 6px;
    margin-bottom: 23px;
    width: 22px;
    height: 22px;
  }
}

/******************************************************************************
 *
 * Footer
 *
 ******************************************************************************/

.footer {
  clear: both;
  margin-top: 0.9em;
  padding-bottom: 3em;
  padding: 2em 0 3.1em 0em;

  p {
    border-top: 1px solid #b57e46;
    color: #8c694b;
    display: inline-block;
    font-size: 12px;
    font-weight: 400;
    padding-top: 12px;
  }
}